import React, { useState, useEffect } from 'react';
import SubLayout from '../common/SubLayout';

function Join() {
	const userVal = {
		userId: '',
		email: '',
		password1: '',
		password2: '',
		name: '',
		gender: null,
		funnels: null,
		comment: '',
	};

	const [Val, setVal] = useState(userVal);
	const [Err, setErr] = useState({});

	const handleChange = (e) => {
		const { name, value } = e.target;
		setVal({ ...Val, [name]: value });
	};

	const check = (Val) => {
		const errs = {};
		const eng = /[a-zA-Z]/;
		const num = /[0-9]/;
		const spc = /[`~!@#$%^&*()_+-=]/;

		if (Val.userId.length < 4) {
			errs.userId = ' 4   ';
		}

		if (Val.email.length < 8 || !/@/.test(Val.email)) {
			errs.email = ' (@)  8   ';
		}

		if (
			Val.password1.length < 8 ||
			!eng.test(Val.password1) ||
			!num.test(Val.password1) ||
			!spc.test(Val.password1)
		) {
			errs.password1 =
				' , ,   8   ';
		}

		if (Val.password1 !== Val.password2 || !Val.password2) {
			errs.password2 = '   ';
		}

		if (!Val.name) {
			errs.name = '  ';
		}

		if (!Val.gender) {
			errs.gender = '  ';
		}

		if (!Val.funnels) {
			errs.funnels = '     ';
		}
		if (Val.comment.length < 20) {
			errs.comment = ' 20   ';
		}

		return errs;
	};

	const handleGender = (e) => {
		const { name } = e.target;
		const isCheck = e.target.checked;
		setVal({ ...Val, [name]: isCheck });
	};

	const handleCheck = (e) => {
		let isCheck = false;
		const { name } = e.target;
		const inputs = e.target.parentElement.querySelectorAll('input');

		inputs.forEach((el, idx) => {
			if (el.checked) isCheck = true;
		});

		setVal({ ...Val, [name]: isCheck });
	};

	const handleSubmit = (e) => {
		e.preventDefault();
		setErr(check(Val));
	};

	useEffect(() => {
		console.log(Err);
	}, [Err]);

	return (
		<SubLayout name={'Join'} color={'#0066ff'}>
			<article>
				<textarea>
					Lorem ipsum dolor sit amet consectetur adipisicing elit. Recusandae
					tenetur non dolores voluptatem impedit laudantium veritatis
					doloremque? Facere fuga ab non minima sit! Sapiente, libero doloribus
					enim dignissimos natus delectus quibusdam autem, amet nesciunt,
					eveniet quo facere quod molestiae numquam.
				</textarea>
				<input type='checkbox' />
				<label htmlFor='agree'> ?</label>
			</article>

			<form onSubmit={handleSubmit}>
				<fieldset>
					<legend className='sr-only'></legend>
					<table>
						<caption className='sr-only'></caption>
						<tbody>
							{/* Id  */}
							<tr>
								<th scope='row'>
									<label htmlFor='userId'>ID</label>
								</th>
								<td>
									<input
										type='text'
										name='userId'
										id='userId'
										placeholder='  '
										value={Val.userId}
										onChange={handleChange}
									/>
									<span className='err'>{Err.userId}</span>
								</td>
							</tr>
							{/*   */}
							<tr>
								<th scope='row'>
									<label htmlFor='password1'>password</label>
								</th>
								<td>
									<input
										type='password'
										name='password1'
										id='password1'
										placeholder='  '
										value={Val.password1}
										onChange={handleChange}
									/>
									<span className='err'>{Err.password1}</span>
								</td>
							</tr>
							{/*   */}
							<tr>
								<th scope='row'>
									<label htmlFor='password2'>re password</label>
								</th>
								<td>
									<input
										type='password'
										name='password2'
										id='password2'
										placeholder='  '
										value={Val.password2}
										onChange={handleChange}
									/>
									<span className='err'>{Err.password2}</span>
								</td>
							</tr>
							{/* email  */}
							<tr>
								<th scope='row'>
									<label htmlFor='email'>E-Mail</label>
								</th>
								<td>
									<input
										type='text'
										name='email'
										id='email'
										placeholder='  '
										value={Val.email}
										onChange={handleChange}
									/>
									<span className='err'>{Err.email}</span>
								</td>
							</tr>
							{/*  */}
							<tr>
								<th scope='row'>
									<label htmlFor='name'>name</label>
								</th>
								<td>
									<input
										type='text'
										name='name'
										id='name'
										placeholder='  '
										value={Val.name}
										onChange={handleChange}
									/>
									<span className='err'>{Err.name}</span>
								</td>
							</tr>
							{/*  */}
							<tr>
								<th scope='row'>Gender</th>
								<td>
									<label htmlFor='male'>Male</label>
									<input
										type='radio'
										id='male'
										name='gender'
										onChange={handleGender}
									/>
									<label htmlFor='female'>Female</label>
									<input
										type='radio'
										id='female'
										name='gender'
										onChange={handleGender}
									/>
									<span className='err'>{Err.gender}</span>
								</td>
							</tr>
							{/*  */}
							<tr>
								<th scope='row'></th>
								<td>
									<label htmlFor='sns'>SNS</label>
									<input
										type='checkbox'
										id='sns'
										name='funnels'
										onChange={handleCheck}
									/>
									<label htmlFor='ad'></label>
									<input
										type='checkbox'
										id='ad'
										name='funnels'
										onChange={handleCheck}
									/>
									<label htmlFor='search'></label>
									<input
										type='checkbox'
										id='search'
										name='funnels'
										onChange={handleCheck}
									/>
									<span className='err'>{Err.funnels}</span>
								</td>
							</tr>
							{/*  */}
							<tr>
								<th scope='row'>
									<label htmlFor='comment'>Comment</label>
								</th>
								<td>
									<textarea
										name='comment'
										id='comment'
										value={Val.comment}
										cols='30'
										rows='10'
										placeholder=' 20   '
										onChange={handleChange}></textarea>
									<span className='err'>{Err.comment}</span>
								</td>
							</tr>
							<tr>
								<th colspan='2'>
									<input type='reset' value='RESET' />
									<input type='submit' value='SUBMIT' />
								</th>
							</tr>
						</tbody>
					</table>
				</fieldset>
			</form>
		</SubLayout>
	);
}

export default Join;
