import React from 'react';
import { useRef, useEffect, useState } from 'react';
import SubLayout from '../common/SubLayout';

function Location() {
	const { kakao } = window;
	const container = useRef(null);
	const [Location, setLocation] = useState(null);
	const [Traffic, setTraffic] = useState(false);
	const option = {
		center: new kakao.maps.LatLng(37.5188715541183, 127.12528957675329),
		level: 3,
	};

	const imageSrc = `${process.env.PUBLIC_URL}/img/location-marker.png`;
	const imageSize = new kakao.maps.Size(51, 67);
	const imageOption = { offset: new kakao.maps.Point(25, 67) };

	const markerPos = new kakao.maps.LatLng(37.5188715541183, 127.12528957675329);

	const markerImage = new kakao.maps.MarkerImage(
		imageSrc,
		imageSize,
		imageOption
	);

	const marker = new kakao.maps.Marker({
		position: markerPos,
		image: markerImage,
	});

	useEffect(() => {
		container.current.innerHTML = '';
		const SJFMap = new kakao.maps.Map(container.current, option);
		setLocation(SJFMap);
		marker.setMap(SJFMap);
	}, []);

	useEffect(() => {
		if (Location) {
			Traffic
				? Location.addOverlayMapTypeId(kakao.maps.MapTypeId.TRAFFIC)
				: Location.removeOverlayMapTypeId(kakao.maps.MapTypeId.TRAFFIC);
		}
	}, [Traffic]);

	return (
		<SubLayout name={'Location'}>
			<div id='map' ref={container}></div>
			<button
				type='button'
				onClick={() => {
					setTraffic(!Traffic);
				}}>
				{Traffic ? '  ' : '  '}
			</button>
			<div className='map-info'>
				<ul>
					<li>
						      ,    
						.      .
						    .
					</li>
					<li>
						
						<ul>
							<li>: 5, 9  3 , 4  </li>
							<li>
								
								<ul>
									<li>
										: 3214, 3720, 3319, 3412, 3216, 340
									</li>
									<li>: 6300, 6006, 6006-1</li>
									<li>: 30, 34, 30-5, 30-3, 70</li>
								</ul>
							</li>
						</ul>
					</li>
					<li>
						 
						<ul>
							<li>   :  12,000/ 10 600</li>
							<li>
								          
								  ,     5,000/ 12,000
								 
							</li>
						</ul>
					</li>
					<li>
						        
						. ,       .
					</li>
				</ul>
			</div>
		</SubLayout>
	);
}

export default Location;
